import React from 'react';
import './index.scss';

interface TitleProps {
  title: string;
  titwidth?: string;
  lineHeight?: string;
  subtitle?: string;
  size?: string;
  subwidth?: string;
  subtop?: string;
  subsize?: string;
  subweight?: any;
}

const TopTitle: React.FC<TitleProps> = ({
  title,
  titwidth,
  lineHeight,
  size = 64,
  subtitle,
  subwidth,
  subtop,
  subsize,
  subweight
}) => {
  return (
    <div style={{ marginBottom: 50 }}>
      <div className="top">
        <div className="left"></div>
        <div
          className="centertext"
          style={{ fontSize: size, width: titwidth, lineHeight }}
        >
          {title}
        </div>
        <div className="right"></div>
      </div>
      <div
        className="toptext"
        style={{
          width: subwidth,
          position: 'relative',
          top: subtop,
          fontSize: subsize,
          fontWeight: subweight
        }}
      >
        {subtitle}
      </div>
    </div>
  );
};

export default TopTitle;
