import React from 'react';
import { Link } from 'react-router-dom';
import { RouterType } from '@/layouts/default';
import Facebook from '../../assets/imgs/imgs/facebook.png';
import Twitter from '../../assets/imgs/imgs/twitter.png';
import Linkedin from '../../assets/imgs/imgs/linkedin.png';
import Telegram from '../../assets/imgs/imgs/telegram.png';
import './index.scss';

const Footer: React.FC<{ routers: RouterType }> = ({ routers }) => {
  return (
    <footer className="footer">
      <div className="container">
        <div className="block">
          <div className="logo"></div>
          <div className="text">
            &copy; {new Date().getFullYear()} Coffee Cloud <br />
            Company Limited
          </div>
        </div>
        <div className="block">
          <div className="title">
            <Link to={routers.solution}>Solutions</Link>
          </div>
          <Link className="text" to={routers.cloudHosting}>
            Cloud Hosting
          </Link>
          <Link className="text" to={routers.webDevelopment}>
            Web Development
          </Link>
          <Link className="text" to={routers.softwareService}>
            Software Service
          </Link>
          <Link className="text" to={routers.dataCenterService}>
            Data Center Service
          </Link>
        </div>
        <div className="block">
          <div className="title">
            <Link to={routers.aboutUs}>About us</Link>
          </div>
          <div className="title">
            <Link to={routers.contactUs}>Contact us</Link>
          </div>

          <div style={{ marginTop: 90 }}>
            <img className="icon" src={Facebook} alt="" />
            <img className="icon" src={Twitter} alt="" />
            <img className="icon" src={Linkedin} alt="" />
          </div>
        </div>
        <div className="block">
          <div className="title" style={{ width: 360 }}>
            Subscribe to Coffee Cloud{' '}
          </div>
          <div className="text" style={{ width: 360 }}>
            Sign up to receive our newsletter to be kept
            <br /> up-to-date on our latest news and features.
          </div>
          <div style={{ marginTop: 60 }}>
            <input
              type="text"
              placeholder="Your email"
              style={{
                backgroundColor: 'white',
                padding: '5px 15px',
                borderRadius: 5,
                width: 360,
                height: 45
              }}
            />
            <img
              className="icon"
              style={{
                position: 'relative',
                top: -33,
                left: 320,
                cursor: 'pointer'
              }}
              src={Telegram}
              alt=""
            />
          </div>
        </div>
      </div>
    </footer>
  );
};

export default React.memo(Footer);
