import React from 'react';

import HostPlan from '@/assets/imgs/imgs/hosting_plan_background.png';
import './index.scss';

interface ContentWrapProps {
  paddingTop?: string;
  paddingRight?: string;
  paddingBottom?: string;
  paddingLeft?: string;
  width?: string;
  backgroundColor?: string;
  height?: string;
  // imgBg?: boolean;
  // pinkBg?: boolean;
}

const ContentWrap: React.FC<ContentWrapProps> = ({
  paddingTop = '60px',
  paddingRight = 'calc((100% - 1140px) / 2)',
  paddingBottom = '110px',
  paddingLeft = 'calc((100% - 1140px) / 2)',
  height,
  width,
  backgroundColor,
  // imgBg = false,
  // pinkBg = false,

  children
}) => {
  return (
    <div
      style={{
        paddingTop,
        paddingRight,
        paddingBottom,
        paddingLeft,
        height,
        width,
        backgroundColor
        // color: imgBg || pinkBg ? '#fff' : '#304659',
        // background: imgBg || pinkBg ? '#ec407a05' : '#fff'
      }}
      className="content-wrap"
    >
      {/* {imgBg && <img src={HostPlan} alt="hosting_plan_background" />} */}
      <div>{children}</div>
    </div>
  );
};

export default ContentWrap;
