import Title from '@/components/title';
import ContentWrap from '@/components/content-wrap';
import { Link } from 'react-router-dom';

import './inedex.scss';

const cardData = [
  {
    title: 'C100',
    hkd: '320',
    sys: 'Linux',
    core: '4',
    memory: '8',
    ssd: '160',
    transfer: '5000'
  },
  {
    title: 'C200',
    hkd: '640',
    sys: 'Linux or Windows',
    core: '6',
    memory: '16',
    ssd: '320',
    transfer: '6000'
  },
  {
    title: 'C300',
    hkd: '1280',
    sys: 'Linux or Windows',
    core: '8',
    memory: '32',
    ssd: '640',
    transfer: '7000'
  }
];

const HostingPlans = ({ goSubmit }) => {
  return (
    <ContentWrap backgroundColor="#ec407a05" height="1000px">
      <Title
        width="770px"
        marginTop="80px"
        title="VPS Cloud Servers Hosting Plans"
        content={[
          'With almost a decade of professional services experience, our international team is here to support your Enterprise-Level Cloud Services Solutions. Compare and choose the best Cloud Service Plan that suits your needs and requirements. If you need further details or information please do not hesitate to contact our Sales team at the link below. '
        ]}
        contentTextAlign="center"
      />
      <div className="card-wrap">
        {cardData.map((d, index) => (
          <div
            className="shadow"
            key={index}
            // onClick={() => {
            //   goSubmit(`Cloud VPS Plan ${d.title}`);
            // }}
          >
            <div className="font-24 font-bold">Cloud VPS Plan</div>
            <div className="font-24 font-bold">{d.title}</div>
            <div style={{ margin: '30px 0 10px' }} className="font-bold">
              <span className="font-24 font-bold">HKD</span>
              <span style={{ fontSize: '50px' }} className="font-24 font-bold">
                {d.hkd}
              </span>
            </div>
            <div
              style={{ marginBottom: '15px' }}
              className="font-16 font-bold grey-color"
            >
              per month
            </div>
            <div className="row">{d.sys}</div>
            <div className="row">{d.core} Core Processor</div>
            <div className="row">{d.memory}GB Memory</div>
            <div className="row">{d.ssd}GB SSD with HA</div>
            <div className="row">{d.transfer}GB Transfer</div>

            <button
              style={{ position: 'relative', top: -15 }}
              onClick={() => {
                window.location.href = '/contact';
                console.log(location);
              }}
            >
              <a href="/contact">Order Now</a>
            </button>
            {/* <button
              onClick={() => {
                goSubmit(`Cloud VPS Plan ${d.title}`);
              }}
            >
              Order now
            </button> */}
          </div>
        ))}
      </div>

      {/* <div
        style={{
          color: '#1e1e1e',
          textAlign: 'center',
          fontSize: 42,
          marginTop: 95
        }}
      >
        Contact our sales team for more details.
      </div>
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <button
          style={{
            width: 192,
            height: 36,
            borderRadius: 50,
            marginTop: 30
          }}
        >
          Contact Us Now
        </button>
      </div> */}
    </ContentWrap>
  );
};

export default HostingPlans;
