import { RouterType } from '@/layouts/default';
import React from 'react';
import { useLocation } from 'react-router-dom';
import TopTitle from '../top-title';
import './index.scss';

const Banner: React.FC<{ routers: RouterType; isMobile: boolean }> = ({
  routers,
  isMobile
}) => {
  const { pathname } = useLocation();

  const bannerData = {
    [routers.home]: {
      title: 'Cloud & Security Consulting Services',
      subTitle:
        'We deliver high-quality & cost effective cloud server solution for your business.'
    },
    [routers.solution]: {
      title:
        '“We provide high-quality, affordable Cloud Server Solutions for our enterprises in Hong Kong and around the world.”',
      titwidth: '825px',
      size: '42px',
      lineHeight: '41px'
    },
    [routers.cloudHosting]: {
      title: 'Cloud Hosting',
      subTitle:
        'Coffee Cloud services is packed with unlimited features & flexibility'
    },
    [routers.webDevelopment]: {
      title: 'Web Development',
      subTitle: 'A complete solution for your business and enterprise portal.'
    },
    [routers.softwareService]: {
      title: 'Software Service',
      subWidth: '542px',
      subTitle:
        'Coffee Cloud offers a diverse range of software development solutions for a horizontal tier of industries.'
    },
    [routers.dataCenterService]: {
      title: 'Data Center Service',
      subTitle:
        'Long term cooperations with purpose-built data center to ensure high level of service quality and flexibility for customization.'
    },
    [routers.aboutUs]: {
      title: 'Brewed To Perfection',
      subTitle: 'Learn more about The Coffee Cloud Team'
    },
    [routers.contactUs]: {
      title: 'Get In Touch',
      subWidth: '452px',
      subTitle:
        'Got questions, feedback or requests?              Send us a message and we’ll get back to you soon!'
    },
    [routers.thank]: {
      title: 'Thank you',
      subWidth: '413px',
      subTitle:
        'Thank you for your enquiry. A member of our team will be in touch shortly.'
    }
  };

  const data = bannerData[pathname];
  const isHome = '/home' === pathname;
  if (isMobile) {
    return (
      <>
        <div className="mobile-banner-wrap">
          <div className="blue-color">{data?.title}</div>
          {/* {data?.subTitle.map((d, index) => (
            <div key={index} className="font-16 grey-color">
              {d}
            </div>
          ))} */}
          {isHome && <button>Contact us</button>}
        </div>
        <div
          // style={{
          //   background: `url(${data?.banner}) center / cover no-repeat`
          // }}
          className="mobile-banner-bg"
        ></div>
      </>
    );
  }

  if (isHome) {
    return (
      <section className="home-banner">
        <TopTitle
          title={'First Coffee, then Tech'}
          lineHeight="40px"
          subtop="-55px"
          subweight="bold"
          subsize="32px"
          subtitle={'Taking your Technology to Cloud Nine'}
        />
      </section>
    );
  } else {
    return (
      <section className="banner">
        <TopTitle
          title={data?.title}
          lineHeight={data?.lineHeight}
          titwidth={data?.titwidth}
          size={data?.size}
          subtitle={data?.subTitle}
          subwidth={data?.subWidth}
        />
        {/* <div className="title">{data?.title}</div>
        {data?.subTitle?.map((content, index) => (
          <div key={index}>{content}</div>
        ))} */}
      </section>
    );
  }
};

export default Banner;
