import Title from '@/components/title';
import ContentWrap from '@/components/content-wrap';
import HostingPlans from '@/components/hosting-plans';
import HighlightPoints from '@/components/highlight-points';
import Points from '@/assets/imgs/icon/points.svg';
import Frame from '@/assets/imgs/icon/frame.svg';
import Cloudbg from '@/assets/imgs/imgs/cloud.png';

import './index.scss';

const poins = [
  [
    'SSD-Accelerated',
    'Admin panel for web and email services',
    'Multi php version',
    'MySQL Databases',
    'Free Website Migration',
    'Web Statistics Report',
    'Free DNS Hosting'
  ],
  [
    'SMTP submission Support',
    'IMAP E-mail Support',
    'Webmail Support',
    'Antispam and Antivirus Filtering',
    'Forwarding and Autoresponders for E-mail',
    'DKIM',
    'SPF'
  ]
];
const Cloud = ({ goSubmit, isMobile }) => {
  return (
    <>
      <ContentWrap backgroundColor="#fafafa">
        <Title
          title="Web Hosting Features"
          contentWidth="910px"
          marginTop="50px"
          content={[
            'Coffee Cloud is one of the leading cloud service providers in Hong Kong, offering a wide range of reliable yet affordable Cloud Web Hosting Solutions to meet your unique enterprise and business needs.'
          ]}
        />
        <div className="point-wrap">
          <embed
            style={{ position: 'absolute', left: 390, top: -9 }}
            src={Points}
            type="image/svg+xml"
          />
          <HighlightPoints title="Web Hosting Features" points={poins[0]} />
          <embed
            style={{ position: 'absolute', left: 920, top: -19 }}
            src={Frame}
            type="image/svg+xml"
          />
          <HighlightPoints
            style={{ backgroundColor: 'white' }}
            title="Email services Features"
            points={poins[1]}
          />
        </div>
        <img
          style={{
            position: 'absolute',
            right: -381,
            bottom: -110,
            width: 575,
            zIndex: -1
          }}
          src={Cloudbg}
          alt=""
        />
      </ContentWrap>
      <HostingPlans goSubmit={goSubmit} />
    </>
  );
};

export default Cloud;
