import Title from '@/components/title';
import DescriptionGrid from '@/components/description-grid';
import ContentWrap from '@/components/content-wrap';
import WebSet from '@/assets/imgs/imgs/set.png';

const Software = ({ isMobile }) => {
  return (
    <ContentWrap
      paddingTop="75px"
      paddingBottom="125px"
      height="1950px"
      backgroundColor="#fafafa"
    >
      <div
        className="software-service"
        style={{ width: 950, margin: 'auto', position: 'relative' }}
      >
        <Title
          title="Software service"
          width="840px"
          content={[
            'Our Hong Kong-based software development team offers a diverse range of solutions for a horizontal tier of industries. We had a sophisticated on-site and off-site model that is on par with global well-accepted standards.',
            'We present solutions that are personal and productive. All our solutions are customizable and can be crafted to suit your business. Over the years, Coffee Cloud has offered a range of solutions that are proven to be very successful.'
          ]}
        />
        <DescriptionGrid
          borderRadius="15px"
          boxShadow="0 0 10px 5px #f0f0f0"
          itemHeight="330px"
          items={[
            {
              title: 'Enterprise Resource Planning (ERP) System',
              descriptions: [
                'Our team develops custom solutions for Intranets & Extranet development, Sharepoint integration and knowledge management.'
              ]
            },
            {
              title: 'Procurement Management System',
              descriptions: [
                'Coffee Cloud procurement management system is an all-in-one software solution that is flexible and highly customizable for your unique business requirements.'
              ]
            },
            {
              title: 'Enterprise Asset Management System',
              descriptions: [
                'Introducing the robust enterprise software solution  - Where you will find the solutions to all the challenges you experience related to asset.'
              ]
            },
            {
              title: 'Training Management System (TMS)',
              descriptions: [
                'Our software design is user-friendly and flexible which means your employees will find it easy to adapt to the new software and you’ll have the ability to customize Coffee Cloud TMS.'
              ]
            },
            {
              title: 'Learning Management System',
              descriptions: [
                'Coffee Cloud LMS helps you to cut cost on conducting laborious trainings across time and space. Not only that, Coffee Cloud LMS primarily serves as a fantastic platform for de-centralized.'
              ]
            },
            {
              title: 'Financial Assistance Scheme System',
              descriptions: [
                'Coffee Cloud FAS is the only Financial Assistance Scheme Management system in the market that allows you to manage your financial assistance schemes, from application calling.'
              ]
            },
            {
              title: 'Facility & Property Management System',
              descriptions: [
                'Coffee Cloud Property ensures an end-to-end management of property details. This software allows you to streamline all processes involved in this industry, including the financial details.'
              ]
            },
            {
              title: 'Project Management System',
              descriptions: [
                'Coffee Cloud Project is a Project Management system, better than the best, proven for its exceptional capabilities, and is just what you need. It is web-based and is designed.'
              ]
            },
            {
              title: 'Human Resource Management System',
              descriptions: [
                'Coffee Cloud HRMS skilfully manages the HR requirements of your company / organization employing features such as Employee information management, Travel claims, Leave management.'
              ]
            },
            {
              title: 'E-commerce Management System',
              descriptions: [
                'Coffee Cloud provides adaptable and scalable e-commerce solutions that cater to the most complex requirement for any given industry. Our competent project management methodology.'
              ]
            },
            {
              title: 'Business Intelligence System',
              descriptions: [
                'Coffee Cloud BI is a smart system that helps in collating, analyzing, chunking, and presenting business information in an accessible manner. This transformed information helps you to improve.'
              ]
            },
            {
              title: 'Case Management System',
              descriptions: [
                'Coffeee Cloud Case is a transformative solution that resolves many inefficiencies related to managing hundreds of cases and motivating front-line non-profit service providers.'
              ]
            }
          ]}
        />
        <img
          style={{ position: 'absolute', right: -476, bottom: 0, height: 583 }}
          src={WebSet}
          alt=""
        />
      </div>
    </ContentWrap>
  );
};

export default Software;
